<template>
  <div class="patent-rank-container">
    <div class="top">
      <div class="top-left">
        <img
          src="static/img/map/up.svg"
        />
        <div class="title">软著数量排行</div>
      </div>
      <div class="top-right">
        <el-button title="查看更多" type="primary" icon="el-icon-more" circle @click="showMore"></el-button>
      </div>
    </div>
    <div class="content-table-border">
      <el-table
        v-loading="loading"
        element-loading-spinner="el-icon-loading"
        :data="data"
        :show-header="false"
        size="mini"
      >
        <el-table-column
          type="index"
          label="序号"
          align="center"
        >
          <template slot-scope="scope">
            <el-badge
              :value="scope.$index + 1"
              :class="getShowColor(scope.$index + 1)"
            />
          </template>
        </el-table-column>
        <el-table-column
          prop="entName"
          label="街道名"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="num"
          label="数量"
          align="center"
        >
          <template slot-scope="scope">
            {{ scope.row.num ? scope.row.num : 0}}
          </template>
        </el-table-column>
        <template slot="empty">
          <div class="table-empty-container">
            <div class="table-empty-icon"></div>
            <div class="table-empty-tips">暂无数据</div>
          </div>
        </template>
      </el-table>
    </div>
  </div>
</template>
<script>
  export default {
    title: '软著数量排行',
    data () {
      return {
        loading: false,
        data: []
      }
    },
    mounted () {
      this.initData()
    },
    methods: {
      initData () {
        this.$http.get('/statistics/softRank', '', {customMessage: true}).then(res => {
          if (res.code === this.$root.codeSuccess) {
            this.data = [...res.content]
          }
        })
      },
      getShowColor (val) {
        if (val > 3) {
          return 'rank-badge-4'
        } else {
          return 'rank-badge-' + val
        }
      },
      showMore () {
        this.$router.push({name: 'CompanyManage'})
      }
    }
  }
</script>
<style lang="scss" scoped>

.patent-rank-container {
  height: 100%;
  width: 33%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  .top {
    height: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 10px;
    .top-left {
      display: flex;
      flex-direction: row;
      img {
        margin-right: 10px;
        width: 26px;
        height: 26px;
      }
      .title {
        font-size: 20px;
        font-weight: bold;
      }
    }

  }
  /deep/ .rank-badge-1 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #1890ff;
    }
  }
  /deep/ .rank-badge-2 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #57d0ff;
    }
  }
  /deep/ .rank-badge-3 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #8783F5;
    }
  }
  /deep/ .rank-badge-4 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #aec6ff;
    }
  }
}

</style>



