<template>
  <div class="street-company-container">
    <el-table
      v-loading="loading"
      element-loading-spinner="el-icon-loading"
      :data="data"
      size="mini"
    >
      <el-table-column
        type="index"
        label="序号"
        align="center"
      >
        <template slot-scope="scope">
          <el-badge
            :value="scope.$index + 1"
            :class="getShowColor(scope.$index + 1)"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="street"
        label="街道名"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="num"
        label="数量"
        align="center"
      >
      </el-table-column>
      <el-table-column label="企业数量占比(%)" align="center">
        <template slot-scope="scope">
          <!--<el-progress-->
            <!--:percentage="(( scope.row.total / systemMaxTotal) * 100)"-->
            <!--:format="systemTopProgress"-->
            <!--:class="getShowSystemProgressColor(scope.$index + 1)"-->
          <!--&gt;</el-progress>-->
          <el-progress  :stroke-width="10" :percentage="(scope.row.ratio.toFixed(2) * 100)"
                        :class="getProgressColor(scope.$index + 1)"></el-progress>
        </template>
      </el-table-column>
      <template slot="empty">
        <div class="table-empty-container">
          <div class="table-empty-icon"></div>
          <div class="table-empty-tips">暂无数据</div>
        </div>
      </template>
    </el-table>
  </div>
</template>
<script>
  export default {
    title: '各街道科技企业数量',
    data () {
      return {
        loading: false,
        data: []
      }
    },
    mounted () {
      this.initData()
    },
    methods: {
      initData () {
        this.$http.get('/statistics/streetRank', '', {customMessage: true}).then(res => {
          if (res.code === this.$root.codeSuccess) {
            this.data = [...res.content]
          }
        })
      },
      getShowColor (val) {
        if (val > 3) {
          return 'street-company-badge-4'
        } else {
          return 'street-company-badge-' + val
        }
      },
      getProgressColor (val) {
        if (val > 3) {
          return 'street-company-progress-4'
        } else {
          return 'street-company-progress-' + val
        }
      }
    }
  }
</script>
<style lang="scss" scoped>

.street-company-container {
  height: 399px;
  /deep/ .el-table {
    height: 100%;
  }

  /deep/ .street-company-badge-1 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #1890ff;
    }
  }
  /deep/ .street-company-badge-2 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #57d0ff;
    }
  }
  /deep/ .street-company-badge-3 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #8783F5;
    }
  }
  /deep/ .street-company-badge-4 {
    width: 10px;
    height: 10px;
    .el-badge__content {
      background-color: #aec6ff;
    }
  }
  /deep/ .street-company-progress-1 {
    .el-progress__text {
      font-size: 5px;
    }
    .el-progress-bar__outer {
      background-color: #ebeef5;
    }
    .el-progress-bar__inner {
      background-color: #1890ff;
    }
  }
  /deep/ .street-company-progress-2 {
    .el-progress__text {
      font-size: 5px;
    }
    .el-progress-bar__outer {
      background-color: #ebeef5;
    }
    .el-progress-bar__inner {
      background-color: #57d0ff;
    }
  }
  /deep/ .street-company-progress-3 {
    .el-progress__text {
      font-size: 5px;
    }
    .el-progress-bar__outer {
      background-color: #ebeef5;
    }
    .el-progress-bar__inner {
      background-color: #8783F5;
    }
  }
  /deep/ .street-company-progress-4 {
    .el-progress__text {
      font-size: 5px;
    }
    .el-progress-bar__outer {
      background-color: #ebeef5;
    }
    .el-progress-bar__inner {
      background-color: #aec6ff;
    }
  }
}

</style>



